import { db, auth } from './firebase';

// User API

export const doCreateUser = (id, username, email) =>
  db.ref(`users/${id}`).set({
    username,
    email,
  });

export const onceGetUsers = () => db.ref('users').once('value');

export const createNewList = (name) => {
  const uid = guidGenerator();
  const userId = auth.currentUser.uid;
  const userName = `${auth.currentUser.displayName} (${auth.currentUser.email})`;;

  let listOfPromises = [];

  listOfPromises.push(db.ref('lists/'+ uid).set({
    name: name,
    userList: [{
      userId: userId,
      userName: userName
    }]
  }));

  listOfPromises.push(db.ref(`users/${userId}/lists`).push({uid}));

  return Promise.all(listOfPromises).then(() => uid);
}
function guidGenerator() {
    var S4 = function() {
       return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
    };
    return (S4()+S4()+S4()+S4()+S4()+S4()+S4()+S4());
}

const fromObjectToList = object =>
  object
    ? Object.keys(object).map(key => ({ ...object[key], index: key }))
    : [];

export const readLists = () => {
  const userId = auth.currentUser.uid;
  return db.ref(`users/${userId}/lists`).once('value').then(snapshot => {
    return snapshot.val();
  }).then((snapshot) => {
    let listOfPromises = [];
    fromObjectToList(snapshot).forEach((list) => {
      listOfPromises.push(readList(list.uid).then((topush) => {
        topush.uid = list.uid;
        return topush;
      }))
    })
    return Promise.all(listOfPromises);
  }).then(snapshot => {
    return fromObjectToList(snapshot);
  });
}

export const readList = (id) => {
  return db.ref(`lists/${id}`).once('value').then(snapshot => {
    const newList = snapshot.val();
    newList.userList = fromObjectToList(snapshot.val().userList);
    return newList;
  });
}

export const joinList = (listId) => {
  const userId = auth.currentUser.uid;
  console.log();
  const userName = `${auth.currentUser.displayName} (${auth.currentUser.email})`;

  const newList = db.ref('lists/'+ listId + '/userList').push({
    userId: userId,
    userName: userName
  });

  db.ref(`users/${userId}/lists`).push({
    uid: listId
  });

  return newList;
}


export const changeListName = (id, newName) => {
  var updates = {};
  updates[`lists/${id}/name`] = newName;
  return db.ref().update(updates);
}

export const deleteUserFromList = (listId, userId, userIndex) => {
  db.ref(`users/${userId}/lists`).once('value').then(snapshot => {
    fromObjectToList(snapshot.val()).forEach((list) => {
      if(list.uid === listId) {
        db.ref(`users/${userId}/lists/${list.index}`).remove();
      }
    })
  });
  return db.ref(`lists/${listId}/userList/${userIndex}`).remove();
}

export const draw = (listId) => {
  // const currentUserId = auth.currentUser.uid;
  return db.ref(`lists/${listId}`).once('value').then(snapshot => {
    const userList = fromObjectToList(snapshot.val().userList);
    var updates = {};
    // console.log(userList)
    //delete current user from lists
    userList.forEach((user, index) => {
      const listToDraw = userList.filter((userTofilter)=>{
        //filter current user
        return (userTofilter.index !== user.index) &&
        //or is already picked
        (!userTofilter.isPicked)
      })

      let item = Math.floor(Math.random()*listToDraw.length);

      //one before the last one BUGFIX
      if(listToDraw.length === 2 && index === userList.length -2) {
        item = listToDraw.length - 1;
      }

      userList.forEach((it) => {
        if(it.index === listToDraw[item].index) {
          it.isPicked = true;
        }
      })

      user.userDraw = listToDraw[item].userName

      if(auth.currentUser.uid === user.userId) {
        userList.myDraw = listToDraw[item].userName;
      }

      updates[`lists/${listId}/userList/${user.index}/userDraw`] = listToDraw[item].userName;
      updates[`lists/${listId}/userList/${listToDraw[item].index}/isPicked`] = true;

    })

    updates[`lists/${listId}/isDraw/`] = true;
    userList.isDraw = true;
    return db.ref().update(updates);
  });
}
