import React from 'react';

import { auth } from '../../firebase';
import configFile from '../../../gatsby-config';
import { navigate } from "gatsby";

const SignOutButton = () => (
  <button type="button" onClick={() => {auth.doSignOut().then(() => {navigate(configFile.pathPrefix + routes.HOME);})}}>
    Sign Out
  </button>
);

export default SignOutButton;
